import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";
import ProductOfferPriceTooltipComponent from "eshop/modules/simfree/components/ProductOfferPriceTooltipComponent"

export default class MultiCartItemMainPriceMobileComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            this.props.price ? (
            <div className="u-table u-left">
                <span className="u-table-cell">
                    <span className="opl-price-v2">
                       <span className="opl-price-v2__part">
                           <span className="opl-price-v2__whole">{ this.getPartsOfPrice(this.props.price.price)[0] }</span>
                       </span>
                       <span className="opl-price-v2__part">
                           <span className="opl-price-v2__separator">,</span>
                           <span className="opl-price-v2__decimal">{ this.getPartsOfPrice(this.props.price.price)[1] }</span>
                           <span className="opl-price-v2__suffix">{ this.props.price.currency }/mc</span>
                       </span>
                    </span>
                </span>
                <span className="u-table-cell u-vertical-top">
                        {this.props.tooltip && renderTooltip(this.props.tooltip)}
                </span>
            </div>
        ) : null);
    }
}
const renderTooltip = ({id = 'defaultID', ...tooltipValues}) => {
    return <ProductOfferPriceTooltipComponent key={id} {...tooltipValues}/>
}